.switch{
    background: #343058;
    border-radius: 1000px;
    border:none;
    position: relative;
    cursor: pointer;
    display: flex;
    outline: none;
    
   &::after{
    content: '';
    display: block;
    width: 30px;
    height: 30px;
    position: absolute;
    background: #F1F1F1;
    top:0;
    left: 0;
    right: unset;
    border-radius: 100px;
    transition: -3s ease all;
    box-shadow: 0px 0px 2px 2px rgba(0,0,0,2);
   }

   &.active{
    background: rgb(252, 160, 239);
    color: #000;
    &::after{
        right: 0;
        left: unset;
    }
   }

    span{
        width: 30px;
        height: 30px;
        line-height: 30px;
        display: block;
        background: none;
        color: #fff;
    }
}