@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@300&display=swap);
*{margin:0;padding:0;font-family:'Rubik';font-size:1rem;box-sizing:border-box}@media all and (max-width: 320px){*{font-size:0.8rem}}.light{--theme-page-background: #FFF;--theme-page-title: #c8b6ff;--theme-card-background:#ffd6ff;--theme-card-title: #b8c0ff;--theme-card-text: #1e1e24;--theme-newCard-background:#c8b6ff;--theme-newCard-title: #1e1e24;--theme-newCard-text: #1e1e24;--theme-buttom-background: #e7c6ff;--theme-buttom-border:#c8b6ff;--theme-border: white;--theme-input-background: #ffd6ff;--theme-input-border: #e7c6ff;--theme-input-text: #1e1e24}.dark{--theme-page-background: #1e1e24;--theme-page-title: #c8b6ff;--theme-card-background:#3f3f4d;--theme-card-title: #b8c0ff;--theme-card-text:#e7c6ff ;--theme-newCard-background:#3f3f4d;--theme-newCard-title: #b8c0ff;--theme-newCard-text: #3f3f4d;--theme-buttom-background: #e7c6ff;--theme-buttom-border:#c8b6ff;--theme-border: white;--theme-input-background: #1e1e24;--theme-input-border: #e7c6ff;--theme-input-text: #c8b6ff}.page{padding:2rem;background:var(--theme-page-background);color:var(--theme-page-text)}.header{display:flex;flex-direction:column;align-items:center;padding-bottom:0.5rem}@media all and (min-width: 768px){.header{flex-direction:row;justify-content:space-between}}.header__title{font-size:2rem;color:var(--theme-page-title);padding:0.5rem}.header__search{border:2px solid var(--theme-input-border);border-radius:0.5rem;background-color:var(--theme-input-background);margin:0.5rem 2rem;padding:3px;transition:all ease 0.3s;color:var(--theme-card-text)}.header__search:focus{border-color:var(--theme-page-title);background-color:var(--theme-buttom-background)}@media all and (min-width: 768px){.header__search{margin-right:0}}.club__list{display:flex;flex-direction:column}.club__item{margin-bottom:1rem;padding:1rem;width:100%;list-style:none;border-radius:1rem;border:1px solid var(--theme-card-title);background-color:var(--theme-card-background);box-shadow:rgba(99,99,99,0.2) 0px 2px 8px 0px;font-weight:bold;color:var(--theme-card-text);display:flex;flex-direction:row;justify-content:space-between}.club__item>*{margin:3px 0}.club__item:hover{box-shadow:rgba(99,99,99,0.2) 0px 5px 10px 5px}.club__label{display:inline-block;font-weight:normal;color:#fff}.club__name{font-weight:bold;font-size:x-large}.club__name:hover{color:var(--theme-newCard-background)}@media all and (max-width: 320px){.club__name{font-size:small}}.club__delete{background-color:var(--theme-buttom-background);border:solid var(--theme-border);border-radius:2rem;color:var(--theme-newCard-text);font-weight:bolder;width:30px;height:30px;display:flex;justify-content:center;align-items:center;cursor:pointer}.club__delete:hover{border-color:var(--theme-newCard-background);background-color:var(--theme-newCard-text);color:var(--theme-card-title)}.new-club__form{display:flex;flex-direction:column;margin-bottom:1rem;padding:1rem;width:100%;list-style:none;border-radius:1rem;border:1px solid var(--theme-card-title);background-color:var(--theme-newCard-background);box-shadow:rgba(99,99,99,0.2) 0px 2px 8px 0px}.new-club__title{padding-bottom:1rem;font-size:1.5rem;color:var(--theme-newCard-title)}@media all and (max-width: 320px){.new-club__title{font-size:small}}.new-club__label{font-weight:bold;display:flex;padding:0.5rem 0;justify-content:flex-start;align-items:center;color:var(--theme-newCard-title)}.new-club__label--input{border:2px solid var(--theme-input-border);border-radius:0.5rem;background-color:var(--theme-input-background);margin:0.5rem 1rem;padding:3px;transition:all ease 0.3s}.new-club__label--input::-webkit-input-placeholder{color:var(--theme-card-text)}.new-club__label--input:-ms-input-placeholder{color:var(--theme-card-text)}.new-club__label--input::placeholder{color:var(--theme-card-text)}@media all and (max-width: 320px){.new-club__label--input{width:50%}}.new-club__label:focus{border-color:var(--theme-card-title);background-color:var(--theme-card-title);box-shadow:rgba(99,99,99,0.2) 0px 2px 8px 0px}.new-club__btn{padding:0.5rem 1.5rem;width:100%;border-radius:0.5rem;border:2px solid var(--theme-border);background-color:var(--theme-buttom-background);font-weight:bold;color:var(--theme-newCard-text);cursor:pointer;transition:all ease 0.3s}.new-club__btn:hover{border-color:var(--theme-newCard-background);background-color:var(--theme-newCard-text);color:var(--theme-card-title)}@media all and (min-width: 768px){.new-club__btn{width:30%}}.copy{font-size:small;display:flex;flex-direction:column;align-items:flex-end}.copy .authorName{display:block}.switch{background:#343058;border-radius:1000px;border:none;position:relative;cursor:pointer;display:flex;outline:none}.switch::after{content:'';display:block;width:30px;height:30px;position:absolute;background:#F1F1F1;top:0;left:0;right:unset;border-radius:100px;transition:-3s ease all;box-shadow:0px 0px 2px 2px #000}.switch.active{background:#fca0ef;color:#000}.switch.active::after{right:0;left:unset}.switch span{width:30px;height:30px;line-height:30px;display:block;background:none;color:#fff}

